body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.movieContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.movieContainer img {
  display: block;
  margin: 0 auto;
}

.movieContainer article {
  padding: 25px;
}

.controls {
  background-color: teal;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

label {
  font-size: 1.2em;
}

#page {
  width: 60px;
  min-width: 60px;
}

.pages {
  padding: 0 15px 0 0;
  margin: 0;
  width: 50px;
}

span {
  height: 30px;
  width: 200px;
}

input,
select {
  font-size: 1.1em;
  text-align: center;
  width: 140px;
  min-width: 140px;
}

h1 {
  padding: 25px;
}

h2,
h3 {
  text-align: center;
}

p {
  padding: 25px;
}